@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #943232;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 55px;
  background-color: black;
}

.nav a {
  color: white;
  text-decoration: none;
  margin: 0 20px;
  /* Espaciado entre los enlaces de navegación */
  font-weight: bold;
  font-size: 23px;
  transition: color 0.3s;
  /* Agregamos una transición para suavizar el cambio de color */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 1);
  /* Sombra de texto para resaltar */
}

.nav a:hover {
  color: #943232;
  /* Cambia el color del texto al pasar el cursor */
}

.titulo {
  color: white;
  font-size: 65px;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 1);
  /* Sombra de texto para resaltar */
  margin-bottom: 20px;
  /* Agregamos un margen inferior para separarlo de otros elementos */
}


.contenedor {
  width: 1200px;
  margin: 0px auto;
}

.divContainerImgs {
  width: 1000px;
  margin: 0px auto;
}

.divContainerImgs img {
  width: 320px;
  height: 400px;
  margin: 0 2.5px;
}


.txtMensaje {
  color: white;
  font-size: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  margin: 20px 0;
  padding: 10px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  border: 1px solid black;
  border-radius: 10px;
  background-color: #943232;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  transition: background-color 0.3s, transform 0.3s;
  /* Transiciones suaves para el cambio de color de fondo y transformación */
}

.txtMensaje:hover {
  background-color: #a03434;
  /* Cambia el color de fondo al pasar el cursor */
  transform: scale(1.05);
  /* Agranda ligeramente el tamaño del párrafo al pasar el cursor */
}


.tituloRecuerdos {
  background-color: black;
  color: white;
  font-size: 60px;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 1);
  /* Sombra de texto para resaltar */
  margin-bottom: 20px;
  border-radius: 10px;
}



.mensajeRecuerdos {
  color: white;
  font-size: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  margin: 20px 0;
  padding: 10px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  border: 1px solid black;
  border-radius: 10px;
  background-color: #000000;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  transition: background-color 0.3s, transform 0.3s;
  /* Transiciones suaves para el cambio de color de fondo y transformación */
}


.mensajeRecuerdos:hover {
  background-color: #a03434;
  /* Cambia el color de fondo al pasar el cursor */
  transform: scale(1.05);
  /* Agranda ligeramente el tamaño del párrafo al pasar el cursor */
}

.divRecuerdos {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra horizontalmente */
  justify-content: center; /* Centra verticalmente */
}


.divRecuerdos img{
  height: 650px;
  width: 900px;
}

.textoGrande {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  resize: none;
}

.enviarBtn {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.enviarBtn:hover {
  background-color: #00a65b;
}

.formulario-container {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}


